









































































































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";

import Url from "../api";
import cookieUtils from "@/utils/cookie";
import utils from "@/utils/utils";
import { validateTel } from "@/utils/validate";

@Component({})
export default class Index extends Vue {
  userInfo: any = {
    id: "",
    userName: "",
    realName: "",
    mobile: "",
    gender: "",
    birthDay: ""
  };

  teamList: Array<any> = [];
  isFirstChange: boolean = true;
  formRules: any = {
    userName: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    realName: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    mobile: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    gender: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    }
  };
  // 当前上传的图片索引
  imgIndex = -1;

  // 自定义上传文件
  customHttpRequest(data: any) {
    let team = data.data;
    let fData = this.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        if (response.data.status) {
          let photo = response.data.data.accessUrl;
          let refArr: any = this.$refs.teamUpload;
          let ref: ElUpload = refArr[this.imgIndex];
          ref.clearFiles();
          this.$http
            .post(Url.updatePhoto, {
              personId: team.personId,
              photo: photo
            })
            .then((res: any) => {
              this.teamList[this.imgIndex].loading = false;
              if (res.data.status) {
                this.$message.success("更新头像成功");
                this.$set(this.teamList[this.imgIndex], "photo", photo);
                // this.getAccountInfo();
              } else {
                this.$message.error(res.data.message);
              }
            })
            .catch(() => {
              this.teamList[this.imgIndex].loading = false;
            });
        } else {
          this.$message.error("图片上传失败");
          this.teamList[this.imgIndex].loading = false;
        }
      })
      .catch(() => {
        this.teamList[this.imgIndex].loading = false;
      });
  }
  //组装图片上传form
  createUploadForm(file: any) {
    let res = {
      config: {
        headers: {
          accessToken: "Bearer " + cookieUtils.get("accessToken"),
          "Content-Type": "multipart/form-data"
        }
      },
      formData: {}
    }; //添加请求头
    let formData = new FormData();
    formData.append("file", file);
    res.formData = formData;
    return res;
  }

  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[], index: number) {
    this.imgIndex = index;
    let { status, message } = validateImage(file.raw);
    let refArr: any = this.$refs.teamUpload;
    let ref: ElUpload = refArr[this.imgIndex];
    if (status) {
      this.teamList[this.imgIndex].loading = true;
      ref.submit();
    } else {
      ref.clearFiles();
      this.$message.error(message);
    }
  }

  //校验字段并保存信息
  handleSave() {
    const userInfo: any = this.$refs.userInfo;
    userInfo.validate((flag: boolean) => {
      if (flag) {
        if (!validateTel(this.userInfo.mobile)) {
          this.$message.warning("手机号格式错误");
          return;
        }
        this.saveForm();
      } else {
        return false;
      }
    });
  }

  //提交基本信息表单
  saveForm() {
    this.$http
      .post(Url.updateBaseUserInfo, {
        realName: this.userInfo.realName,
        mobile: this.userInfo.mobile,
        gender: this.userInfo.gender,
        birthDay: this.userInfo.birthDay
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("保存成功");
          let name = localStorage.getItem("realName");
          if (name !== this.userInfo.realName) {
            localStorage.setItem("realName", this.userInfo.realName);
            qTouch.emit("changeName", this.userInfo.realName);
          }
        } else {
          this.$message.error(res.data.message);
        }
      });
  }

  //判断账户名是否为空
  changeAccount() {
    if (this.userInfo.userName == null || this.userInfo.userName === "") {
      this.isFirstChange = false;
    } else {
      this.isFirstChange = true;
      this.$notify.warning("用户名只能更改一次");
    }
  }

  goChangeAccout() {
    if (this.userInfo.userName == "") {
      this.$message.warning("用户名不能为空!");
    } else {
      this.$http
        .get(Url.changeUserName, {
          params: {
            userName: this.userInfo.userName
          }
        })
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("用户名修改成功");
          } else {
            this.$message.error(res.data.message);
          }
        });
    }
  }

  // 获取账户信息
  getAccountInfo() {
    this.$http.get(Url.getAccountInfo).then((res: any) => {
      if (res.data.status) {
        utils.mapValue(this.userInfo, res.data.data);
        let teamArr: Array<any> = res.data.data.accountTeamVOS;
        localStorage.setItem("realName", res.data.data.realName);
        for (let i in teamArr) {
          teamArr[i].loading = false;
          if (teamArr[i].extInfoVO && teamArr[i].extInfoVO.length) {
            teamArr[i].extInfoVO.forEach((li: any) => {
              if (/\[/.test(li.value)) {
                li.value = JSON.parse(li.value);
              }
            });
          }
        }
        this.teamList = teamArr;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  mounted() {
    this.getAccountInfo();
  }
}
